.advantages {
  margin: 120px auto 120px auto;
  max-width: 1272px;

  &__list {
    margin: 0;
    padding: 0 25px;
    list-style: none;
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 29px;
    column-gap: 30px;
  }
}

@include vp-1023 {
  .advantages {
    margin: 80px 50px 80px 50px;

    &__list {
      padding: 0;
    }
  }
}

@include vp-769 {
  .advantages {
    margin: 0;

    &__list {
      display: flex;
      flex-direction: column;
      gap: 30px;
      margin-top: 50px;
      margin-bottom: 50px;
    }
  }
}
