  min-height: 120px;
  max-width: 1270px;
  padding: 0 85px 0 25px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.header__contacts {
  margin: 0;
  padding: 0;
  display: flex;
  list-style-type: none;
  gap: 30px;
}
.header__button {
  background: none;
  border: 1px solid #f2f5f7;
  color: #ffffff;
}
